import React from 'react';
import {useLocation} from 'react-router-dom'
import { Button} from 'rsuite';
import Total from '../Components/Total';
import { useValue } from '../Misc/ContextProvider';

const Devices = () => {
  
  // const [buylist,updateList] = useState([])
  // const [cartstate,updatecartstate] = useState(0)
  // const [update,updatechange] = useState(0)
  const location = useLocation()
  const search = location.search.slice(1)
  const {products,managecart} = useValue()

  console.log(products,location)
  //FUNCTION TO UPDATE THE BUYLIST
  function addtocart(ev){
    managecart(ev.target.id)
  }
    
    
  return (<div>
    <Total/>
      <div className='container row mx-auto d-flex justify-content-center '>
        {
          products.map((value)=>{
            if (value.categoryId === search){
              return (<div className='col-lg-3 col-md-6 col-sm-1 p-4'>
                <div className=' border items p-4'>
                <img className=' bord w-100' src={value.thumbnail} alt={value.name}></img>
               <h6> NAME: {value.name}</h6>
               <h6>${value.price}</h6>
               <h6 className={value.inStock?"available":"notavailable"}>{value.inStock?"In stock":"Out of stock"}</h6>
               <Button className='mt-3' 
               onClick={addtocart} 
                disabled={value.inStock?false:true} 
                id={value.id} block>
                  Add to cart
                  </Button>
                  </div>
                </div>)
                
            } 
          })
        }
      
    </div>
    
  </div>)
};

export default Devices;
