import React from 'react';
import { Link} from 'react-router-dom';
// import { FlexboxGrid } from 'rsuite';
// import FlexboxGridItem from 'rsuite/esm/FlexboxGrid/FlexboxGridItem';
import { useValue } from '../Misc/ContextProvider';

const Home = () => {
   const {category} = useValue()
   console.log("from home",category)

  return <div className='container-fluid row mx-auto d-flex justify-content-center '> 
           {category.map(({id,name,description}) =>{
              return  (
                 
                 <Link to={`Category/?${id}`} className='text-decoration-none text-dark col-md-6 col-lg-3 col-sm-6 m-5' >
                    <div className=' home-items p-5 shadow  text-center'>
                  <h4 className='w-100 text-white'>{name.toUpperCase()}</h4>
                 </div>
                 </Link>
                 
                 
                 
                 )
            })
         }
  </div>;
};

export default Home;
