import React, { useEffect } from 'react';
import { Button } from 'rsuite';
import { useValue } from '../Misc/ContextProvider';
import { Navigate, useNavigate } from 'react-router-dom';

const Total = () => {
    let navigate = useNavigate()
    const tocheckout = ()=>{
        console.log('from checkout');
        navigate("/Checkout")
    }

    const {currentcart} = useValue()
    console.log("from total",currentcart)
    function itemNumber(){
        let sum = 0
        for(let obj in currentcart){
            sum = currentcart[obj].quantity + sum
        }
        return sum
        
    }
    function Sum(){
        let sum = 0
        for(let obj in currentcart){
            sum = currentcart[obj].price * currentcart[obj].quantity + sum
        }
        return sum
    }
    
  return <div className='container pb-4'>
      <div className='row'>
      <div className='col-1 d-flex justify-content-center'>
          <span className='dot'><h3 className='text-center'>{itemNumber()}</h3></span>
         
      </div>
      <div className='col'>
      <div className='row-2'>
          <h6> {Sum()}</h6>
      </div>
      <div className='row-2'>
          <Button 
          disabled={currentcart[0].id == null?true:false} 
          appearance={currentcart[0].id == null?'ghost':'primary'}
          onClick={tocheckout}>
              Checkout
          </Button>
      </div>
      </div>
      </div>
  </div>;
};

export default Total;
